'use strict';

// MODULES //

import copy from '@stdlib/utils-copy';
import data from './../data/data.json';


// MAIN //

/**
* Returns an object mapping US state capitals to state names.
*
* @returns {Object} capitals to states
*
* @example
* var tbl = table();
* // returns {...}
*/
function table() {
	return copy( data );
}


// EXPORTS //

export default table;
